// project imports
import config from 'config';

// action - state management
import * as actionTypes from './actions';

// ==============================||  REDUCER ||============================== //

const initialStateCustomization = {
    isOpen: [], // for active default menu
    fontFamily: config.fontFamily,
    borderRadius: config.borderRadius,
    opened: true
};

const customizationReducer = (state = initialStateCustomization, action) => {
    let id;
    switch (action.type) {
        case actionTypes.MENU_OPEN:
            id = action.id;
            return {
                ...state,
                isOpen: [id]
            };
        case actionTypes.SET_MENU:
            return {
                ...state,
                opened: action.opened
            };
        case actionTypes.SET_FONT_FAMILY:
            return {
                ...state,
                fontFamily: action.fontFamily
            };
        case actionTypes.SET_BORDER_RADIUS:
            return {
                ...state,
                borderRadius: action.borderRadius
            };
        default:
            return state;
    }
};

const initialStateLocalization = {
    locale: '' // enUS, zhCN
};

const localizationReducer = (state = initialStateLocalization, action) => {
    //console.log(state, action);
    if (action.type === actionTypes.SET_LOCALE) return { ...state, locale: action.locale };
    return state;
};

const initialStateGlobalization = {
    baseData: {}, //
    appData: {} //
};

const globalizationReducer = (state = initialStateGlobalization, action) => {
    //console.log(state, action);
    if (action.type === actionTypes.SET_BASEDATA) return { ...state, baseData: action.baseData };
    if (action.type === actionTypes.SET_APPDATA) return { ...state, appData: action.appData };
    return state;
};

export { localizationReducer, customizationReducer, globalizationReducer };
