import ReactDOM from 'react-dom';

// third party
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import doFetch from 'base/network';
import ma from 'fetch/ma';
import rpc from 'fetch/rpc';
import { hex_md5 } from 'base/md5';

var rpcDataStr = JSON.stringify(rpc);
function prepareMsg(What) {
    let rpcData = JSON.parse(rpcDataStr);
    return rpcData.find((obj) => obj.info.for === What);
} //RPC

async function fetchAPI(paramObj, newHeader) {
    let { loginData } = window;
    if (!loginData) return;
    let queryObj = ma.developerAPIQuery(loginData, paramObj);
    if (newHeader) {
        queryObj.request.header = newHeader;
        if (newHeader['Content-Type'] === 'Form Data') {
            let fd = new FormData();
            for (let [k, v] of Object.entries(queryObj.request.data)) {
                fd.set(k, v);
            }
            queryObj.request.data = fd;
        }
    }
    let msg = await doFetch(queryObj);
    let res = ma.developerAPICheck(msg);
    return res;
}

// 
global.prepareMsg = prepareMsg;
global.doFetch = doFetch;
global.ma = ma;
global.hex_md5 = hex_md5;
global.fetchAPI = fetchAPI;

// project imports
import * as serviceWorker from 'serviceWorker';
import App from 'App';
import { store } from 'store';

// style + assets
import 'assets/scss/style.scss';

// ==============================|| REACT DOM RENDER  ||============================== //

ReactDOM.render(
    <Provider store={store}>
        <BrowserRouter>
            <App />
        </BrowserRouter>
    </Provider>,
    document.getElementById('root')
);

(function getLocalFiles() {
    ['./font/font.css', './favicon.png'].forEach((src) => {
        let link = document.createElement('link');
        link.href = src;
        if (/css/.test(src)) link.rel = 'stylesheet';
        if (/favicon/.test(src)) link.rel = 'icon';
        document.head.appendChild(link);
    });
})();

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
