import PropTypes from 'prop-types';
import { Alert, Snackbar } from '@mui/material';

//
const Notice = ({ type, info, open, onClose }) => {
    //pure functions
    return (
        <Snackbar
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right'
            }}
            open={open}
            onClose={onClose}
            autoHideDuration={type ? 3000 : 10000}
        >
            <Alert variant="filled" severity={type ? 'success' : 'error'}>
                <span>{info}</span>
            </Alert>
        </Snackbar>
    );
};

Notice.propTypes = {
    type: PropTypes.bool,
    info: PropTypes.string,
    open: PropTypes.bool,
    onClose: PropTypes.func
};
export default Notice;
