import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';

// material-ui
import { Typography } from '@mui/material';

// project imports
import NavGroup from './NavGroup';

// assets
import { IconBrandChrome, IconHelp } from '@tabler/icons';
import { Languages } from './Languages';

// ==============================|| SIDEBAR MENU LIST ||============================== //

const MenuList = () => {
    const localization = useSelector((state) => state.localization);
    const browserLocale = /zh/i.test(navigator.language) ? 'zhCN' : 'enUS';
    const [langPack, setLangPack] = useState(Languages[localization.locale || browserLocale]); //

    useEffect(() => {
        setLangPack(Languages[localization.locale || browserLocale]);
        return () => {};
    }, [localization.locale, browserLocale]); //

    const menuItems = {
        items: [
            {
                id: 'main-menu',
                type: 'group',
                children: [
                    {
                        id: 'home',
                        title: langPack.index.home,
                        type: 'item',
                        url: '/',
                        icon: IconBrandChrome,
                        breadcrumbs: false
                    },
                    {
                        id: 'product',
                        title: langPack.index.product,
                        type: 'item',
                        url: '/product',
                        icon: IconBrandChrome,
                        breadcrumbs: false
                    },
                    {
                        id: 'photo',
                        title: langPack.index.photo,
                        type: 'item',
                        url: '/photo',
                        icon: IconBrandChrome,
                        breadcrumbs: false
                    },
                    {
                        id: 'profile',
                        title: langPack.index.profile,
                        type: 'item',
                        url: '/sample-page',
                        icon: IconBrandChrome,
                        breadcrumbs: false
                    },
                    {
                        id: 'service',
                        title: langPack.index.service,
                        type: 'item',
                        url: '/service',
                        icon: IconBrandChrome,
                        breadcrumbs: false
                    },
                    {
                        id: 'help',
                        title: langPack.index.help,
                        type: 'item',
                        url: 'https://codedthemes.gitbook.io/berry/',
                        icon: IconHelp,
                        external: true,
                        target: true
                    }
                ]
            }
        ]
    };
    const navItems = menuItems.items.map((item) => {
        switch (item.type) {
            case 'group':
                return <NavGroup key={item.id} item={item} />;
            default:
                return (
                    <Typography key={item.id} variant="h6" color="error" align="center">
                        Menu Items Error
                    </Typography>
                );
        }
    });

    return <>{navItems}</>;
};

export default MenuList;
